@font-face {
    font-family: 'admin-font';
    src: url('./fonts/admin-font.eot?tm0drt');
    src: url('./fonts/admin-font.eot?tm0drt#iefix') format('embedded-opentype'),
        url('./fonts/admin-font.ttf?tm0drt') format('truetype'),
        url('./fonts/admin-font.woff?tm0drt') format('woff'),
        url('./fonts/admin-font.svg?tm0drt#admin-font') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="font-"],
[class*=" font-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'admin-font' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.font-store:before {
    content: "\e500";
}

.font-shopping_cart:before {
    content: "\e500";
}

.font-info:before {
    content: "\e501";
}

.font-credit_card:before {
    content: "\e9502";
}

.font-payment:before {
    content: "\e9502";
}


.tab-section {
    position: absolute;
    top: -5.55rem;
    /* padding: 0.5rem; */
    right: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 0.75rem;

    button {
        transition: color 0.3s linear;
        padding: 0.5rem;
        position: relative;
        color: black;
        background: transparent;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.25rem;

        span {
            font-size: 1rem;

        }

        &:hover {
            color: #522c81;
        }

        &.active {
            font-weight: 500;
            color: #522c81;

            &::after {
                position: absolute;
                content: "";
                bottom: 0;
                left: 0;
                width: 100%;
                height: 2px;
                background: #522c81;
            }
        }

    }

}


.tab-menu-setting-section {
    position: fixed;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    max-width: 250px;
    /* border: 1px solid; */
    width: 100%;
    background: #fff;
    box-shadow: 0 0 1rem rgba(0, 0, 0, .125);
    min-height: 100px;

    .tab-menu-setting-wrapper {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        .tab-menu-setting-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            .toggle-checkbox-content {
                margin: 0 !important;
            }
        }
    }
}